@media screen and (max-width: 500px) {
.businessSignUp{
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}
.cS{
  text-align: center;
  padding: 10px;
  margin-top: 2vh;
  margin-bottom: 2vh;
  font-family: 'Oswald';
  font-size: 2em;
}

form > h1{
  font-size: 3em;
  text-align: center;
  font-family: 'Oswald';
}

.bforminput {
  width: 20rem;
  padding: 12px;
  border: 1px solid #73a2e9;
  border-radius: 4px;
  resize: vertical;
}

label {
  padding: 12px 12px 12px 0;
  display: inline-block;
}
  
.bcol-25 {
  float: left;
  width: 100%;
  margin-top: 6px;
}

.bcol-75 {
  float: left;
  width: 100%;
  margin-top: 6px;
}

/* Clear floats after the columns */
.brow:after {
  content: "";
  display: table;
  clear: both;
}

.buttonb{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 5px;
  border-radius:5px ;
  height:28px;
  padding: 5px;
}

.buttonb > span{
  text-align: center;
}

.buttonb > button:hover{
  color:#0fb7f4;
  border-radius:5px ;
  border:2.5px solid #0fb7f4;
  height:28px;
  background-color: black;
}
}

@media screen and (min-width: 500px) {
  .businessSignUp{
    background-color: rgba(137, 239, 231, 0.388);
    display: flex;
    flex-direction: row;
    justify-content:center;
    padding: 8px;
    height: 100vh;
  }

  .cS{
    text-align: center;
    padding: 10px;
    margin-top: 2vh;
    margin-bottom: 2vh;
    font-family: 'Oswald';
    font-size: 4em;
  }

  .SU{
    text-align: center;
    font-size: x-large;
  }

  form > h1{
    font-size: 2.5em;
    font-family: 'Oswald';
  }
  
  .bforminput {
    width: 20rem;
    padding: 12px;
    border: 1px solid #73a2e9;
    border-radius: 4px;
    resize: vertical;
  }
  
  label {
    padding: 12px 12px 12px 0;
    display: inline-block;
  }
    
  .bcol-25 {
    float: left;
    width: 100%;
    margin-top: 6px;
  }
  
  .bcol-75 {
    float: left;
    width: 100%;
    margin-top: 6px;
  }
  
  /* Clear floats after the columns */
  .brow:after {
    content: "";
    display: table;
    clear: both;
  }
  
  .buttonb{
    margin-top: 5px;
    border-radius:5px ;
    height:fit-content;
    padding-top: 8px;
  }

  .buttonb > button{
    width: 100%;
    height: 32px;
  }
  
  
  .buttonb > button:hover{
    color:#0fb7f4;
    border-radius:5px ;
    border:2.5px solid #0fb7f4;
    height:32px;
    background-color: black;
  }
  }